import React, {ButtonHTMLAttributes, DetailedHTMLProps} from 'react'
import s from './CanelButton.module.css'

//    , children        
type DefaultButtonPropsType = DetailedHTMLProps<ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>

type CanelButtonPropsType = DefaultButtonPropsType & {
    red?: boolean
    disabled?: boolean
}

const CancelButton: React.FC<CanelButtonPropsType> = (
    {
        red, className,
        ...restProps//       restProps,    children
    }
) => {
    const finalClassName = `${red ? s.red : s.cansel} ${className}`

    return (
        <button
            className={finalClassName}
            {...restProps} //        (children  )
        />
    )
}

export default CancelButton