import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent} from 'react';
import s from './InputSearch.module.css';
import search from "./../../assets/images/search.png";


//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type InputSearchPropsType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string
    // spanClassName?: string
}

const InputSearch: React.FC<InputSearchPropsType> = (
    {
        type, //          
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
         className,
        //  spanClassName,
        ...restProps//       restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange //    onChange
        && onChange(e) //     ( onChange  )

        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        onEnter //    onEnter
        && e.key === 'Enter' //     Enter
        && onEnter() //   
    }

    // const finalSpanClassName = `${s.error} ${spanClassName ? spanClassName : ''}`

    // const finalInputClassName = `${error ? s.errorInput : s.inputSearch} ${className}` // need to fix with (?:) and s.superInput

    return (
        <div className={s.inputSearch}>
            
                        <input
                            type={type}
                            placeholder="Search ..."                          
                            onChange={onChangeCallback}
                            onKeyPress={onKeyPressCallback}
                            className={s.input}
                            {...restProps} //        (value   )
                        />  
                        {/* <img className={s.inputImg} src={search}></img>               */}
        </div>
    )
}

export default InputSearch
